				"html": "<p>In JavaScript, we can divide up our code into reusable parts called functions.</p>\n<p>Here's an example of a function:</p>\n<pre><code>function functionName() {\n  console.log(\"Hello World\");\n}\n</code></pre>\n<p>You can <code>call</code> or <code>invoke</code> this function by using its name followed by parentheses, like this:</p>\n<pre><code>functionName();\n</code></pre>\n<p>Each time the function is called it will print out the message \"Hello World\" on the dev console. All of the code between the curly braces will be executed every time the function is called.</p>\n<p>Here is another example:</p>\n<pre><code>function otherFunctionName (a, b) {\n    return(a + b);\n}\n</code></pre>\n<p>We can <code>call</code> or <code>invoke</code> our function like this:</p>\n<pre><code>otherFunctionName(1,2);\n</code></pre>\n<p>and it will run and return its return value to us.</p>",
